@import "../../../../utills/mixins.scss";

.day-info-item{
  display: flex;
  align-items: center;
  gap: 20px;

  &__wrap {
    display: flex;
    gap: 30px;
    align-items: center;
  }

  &__name {    
    color: #939CB0;
  }

  &__info {
    color: var(#{--theme-text-default});
  }
}

.temp {
  @include icons;
  background-image: url('../../../../images/temp.svg');
}

.pres {
  @include icons;
  background-image: url('../../../../images/pressure.svg');
}

.prec {
  @include icons;
  background-image: url('../../../../images/prec.svg'); 
}

.wind {
  @include icons;
  background-image: url('../../../../images/wind.svg'); 
}
