@import '../../utills/variables.scss';

.popup {
  display: flex;
  gap:30px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(#{--app-background-default});
  box-shadow: var(#{--box-shadow-default});
  box-sizing: border-box;
  border-radius: $border-radius;
  font-size: 14px;
  width: 100%;
  max-width: 300px;
  position: absolute;
  top: 50px;
  left: calc(50% - 150px);
  z-index: 2;
  padding: 20px;

  @media (min-width:650px) {
    padding: 40px;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 750px;
    left: calc(50% - 375px);
    top: calc(1vh -100px)
  }

  @media (min-width:1025px) {
    left: calc(50% - 375px);
    top: calc(50% - 150px);
  }

  &--wrapper {
    display: none;
  }

  &__blur {
    position: absolute;
    width: 100%;
    height: 100vh;
    left: 0;
    z-index: 2;
    background: rgb(91, 139, 228);
    opacity: 0.5;
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    border:1px solid $main-color;
    color: $main-color;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    cursor: pointer;
  
    &:hover {
      transform: rotate(360deg);
      color:rgb(246, 132, 151);
      border:1px solid rgb(246, 132, 151);
    }
  }
  
  &__items {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

 
  
  &__day {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 100%;
    
    &--temp {
      font-size: 70px;
      color: $main-color;
    }

    &--info {
      font-size: 30px;
      color: black;
    }
  }
}
