.home {
  font-size: 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__select {
    max-width: 400px;
    width: 100%;
    height: 50px;
    background: var(#{--main-background-default});
    border-radius: 10px;
    border: transparent;
    font-size: 20px;
    margin-bottom: 20px;
    box-shadow: var(#{--box-shadow-default});
    color: var(#{--theme-text-default});
    padding: 0 20px;
    appearance: none;
    cursor: pointer;

    &:active, &:focus {
      outline: none;
    }

    &--wrap {
      width: 100%;
    }
  }

  &--top {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 50px;
    gap: 50px;

    @media (min-width:650px) {
      flex-direction: row;
      justify-content: center;
    }
  }

  &--bottom {
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;

  }
}