@import '../../../../utills/variables.scss';

.day {
  padding: 20px;
  max-width: 400px;
  width: 100%;
  height: 300px;
  box-shadow: var(#{--box-shadow-default});
  border-radius: 20px;
  box-sizing: border-box;
  background: var(#{--main-background-default});

  &--top {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__weather {
      font-size: 96px;
      color: $main-color;
    }

    &__day{
      font-size: 40px;
      color: var(#{--theme-text-default});
    }

    &__img {
      background-image: url('../../../../images/sun.svg');
      width: 120px;
      height: 120px;
    }
  }

  &--bottom {
    color: #939CB0;
    size: 25px;
    line-height: 32px;

    &__time {
      margin-bottom: 14px;
    }
  }
}   
          
        
          
  