$light-background: #fff;
$main-color: #4793FF;
$border-radius: 20px;

$app-background-dark: black;
$app-background-light:#E5E5E5;

$main-background-dark: #4F4F4F;
$main-background-light: #fff;

$background-dark: #2E3035;
$background-light:rgba(71, 147, 255, 0.2);

$theme-text-dark: white;
$theme-text-light: black;

$box-shadow-light: 2px 5px 25px -3px rgba(180, 180, 180, 0.25);
$box-shadow-dark: 2px 5px 25px -3px rgba(180, 180, 180, 0.25);

:root {
  --app-background-dark: #{$app-background-dark};
  --app-background-light: #{$app-background-light};
  --app-background-default: var(#{var(--app-background-light)});

  --main-background-dark: #{$main-background-dark};
  --main-background-light: #{$main-background-light};
  --main-background-default: var(#{--main-background-light});

  --background-dark: #{$background-dark};
  --background-light: #{$background-light};
  --background-default: var(#{--background-light});

  --theme-text-dark: #{$theme-text-dark};
  --theme-text-light: #{$theme-text-light};
  --theme-text-default: var(#{$theme-text-light});
 
  --box-shadow-dark: #{$box-shadow-dark};
  --box-shadow-light: #{$box-shadow-light};
  --box-shadow-default: var(#{var(--box-shadow-light)});

}
