.header {
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
  margin-bottom: 30px;

  @media (min-width: 650px) {
    flex-direction: row;
    justify-content: space-between;
  }

  &__wrapper {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    background-image: url('../../images/logo.svg');
    background-size: contain;
    background-repeat: no-repeat;
    width: 65px;
    height: 65px;
  }

  &__name {
    font-weight: 700;
    font-size: 25px;
    text-transform: uppercase;
    color: #4793FF;
  }

  &__theme {
    background-image: url('../../images/theme.svg');
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
    width: 24px;
    height: 28px;
  }
}
