@import '../../../../utills/variables.scss';

.day-info {
  display: flex;
  flex-direction: column;
  gap: 30px;
  justify-content: center;
  padding: 20px;
  height: 300px;
  background: var(#{--main-background-default});
  box-shadow: var(#{--box-shadow-default});
  box-sizing: border-box;
  border-radius: $border-radius;
  font-size: 14px;
  padding: auto;
  width: 100%;
  max-width: 400px;
  position: relative;
  overflow: hidden;

  @media (min-width:650px) {
    max-width: 750px;
  }

  &__image {
    position: absolute;
    top: 0;
    right: 0;
  }

  &__items {
    display: flex;
    flex-direction: column;
    gap: 20px;
    z-index: 1;
  }
}
