@import '../../../utills/variables.scss';

.days {
  display: flex;
  padding: 20px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 20px;
  gap: 10px;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  box-sizing: border-box;
  background: var(#{--main-background-default});

  @media (min-width: 1160px) {
    gap: 0;
    justify-content: space-between;
  }

  &__tabs {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;

    &__wrapper {
      display: flex;
      gap: 10px;

      @media (min-width: 650px) {
        gap: 20px;
      }

      @media (min-width: 1000px) {
        gap: 35px;
      }
    }

    &--tab {
      width: 44px;
      height: 30px;
      background: var(#{--main-background-default});
      color: var(#{--theme-text-default});
      box-shadow: var(#{--box-shadow-default});
      border-radius: 5px;
      font-size: 10px;

      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;

      @media (min-width: 650px) {
        width: 144px;
        height: 35px; 
        font-size: 18px;
      }

      &:hover {
        background-color: #4793FF;
        color: #fff;
      }
    }
  }

  &__card {
    padding: 20px;
    box-sizing: border-box;
    box-shadow: var(#{--box-shadow-default});
    width: 149px;
    height: 200px;
    border-radius: 10px;
    line-height: 22px;

    @media (min-width:1000px) {
      width: 140px;
    }

    &__day {
      font-weight:500;
      font-size: 18px;
      color: var(#{--theme-text-default});

      &--info {
        color: #939CB0;
        font-size: 14px;
      }
    }

    &__temp {
      &--day {
        font-size: 18px;
        color: var(#{--theme-text-default});
      }

      &--night,
        &--info {
        color: #939CB0;
        font-size: 13px;
    }
    }
  }
}