@import './utills/variables.scss';

body {
  background: var(#{--app-background-default});
}

.app {
  font-family: 'Montserrat', sans-serif;
  max-width: 1200px;
  margin: 0 auto;

  &-wrapper {
    font-size: 18px;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    position: relative;

    @media (min-width: 650px) {
      display: block;
      padding: auto;
    }
  }
}